.c-donations-hero {
  background-color: $s-color--background-subdued;

  &__outer{
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__curve-container {
    transform: translateY(100%);
  }


  &__inner {
    display: flex;
    flex-direction: column-reverse;
    padding:2.75rem $s-gutter--edge-mobile 2.8125rem ;
    align-items: center;
  }

  &__graphic-container {
    position: relative;
    margin-bottom: 2.875rem;
    min-width: 21.1875rem;
    max-width: 37.5rem;
    text-align: center;
  }

  &__content{
    max-width: 37.5rem;
  }

  &__title {
    margin-bottom: 1.25rem;
    text-align: left;
  }  

  &__total-wrapper {
    @include t-absolute-center-x-y();
    left: 41%;
    top:53.5%;
    color: $s-color--font-invert;
  }

  &__text {
    padding-left: 2.625rem;
    text-align: left;
    max-width: 40rem;
  }

  &__heading{
    margin-bottom: 0.1875rem;
  }



  @include t-media-query($s-medium-up) {


    &__inner {
      flex-direction: row;
      padding: 2.4375rem 8%;
      align-items: center;
      justify-content: space-between;

    }

    &__graphic-container {
      max-width: 40rem;
      width: 50%;
    }

    &__content{
      max-width: unset;
      width: 50%;
    }
  }


  @include t-media-query($s-large-up) {
    &__inner {
      flex-direction: row;
      padding: 3.3125rem 14% 12.5rem;
      align-items: center;
      justify-content: space-between;
      gap: 0.625rem;
    }

    &__title{
      margin-top: 6.875rem;
      margin-bottom: 2.25rem;
    }

    &__text{
      padding-left: 7.375rem;
    }

    &__graphic-container{
      margin: 0 -3.8125rem 0 0;
    }

    &__heading{
      margin: .5rem 0 1.5625rem;
    }
  }
}